.Main {
    display: flex;
    margin: 0 auto;
    background-color: #e8ebf4;
    height: 100%;
    width: 90%;
    padding-top: 30px;
}

.Main-card {
    display: grid;
    height: 200px;
    width: 30%;
    background-color: white;
    border-radius: 10px;
}

.Main-title {
    background: linear-gradient(90deg, rgba(74,40,100,1) 0%, rgba(203,61,83,1) 100%, rgba(239,67,78,1) 100%);
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-around;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}


.Main-card__icon {

    width: 30px;
    height: 30px;
    margin-left: 5px;
}

.Main-card__text {
    color: white;
}


.Main-card__dates {
    display: grid;
    justify-content: center;
    align-items: center;
    padding: 0px;
}

.Main-card__cash {
    display: flex;
    justify-content: center;
    font-size: 25px;
    color:  rgb(74,40,100);
    
}

.Main-card li {
    color:

}

.Main-card__date {
    
    display: flex;
    font-size: small;
    justify-content: center;
    margin-top: -2px;
    color: #111e6c;
}



.Main-buttons {
  
    width: 70%;
    margin-left: 50px;
    
}

.Main-buttons__list {
    height: 40px;
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Main-buttons__list button {
    height: 25px;
    width: 150px;
    background-color: white;
    border: transparent;
    cursor: pointer;
    color: #111e6c;
    border-radius: 10px;
    font-size: 15px;
}


.Main-buttons__list button:hover {
    background-color: #969696;
}

.Main-buttons__filter {
    display: flex;
    height: 40px;
    margin-top: 10px;
    justify-content: flex-end;
}
.Main-buttons__filter button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin: 0px;
    width: 150px;
    background-color: white;
    border: transparent;
    cursor: pointer;
    
}

.filter {
    margin-left: 5px;
    height: 20px;
    width: 20px;
}
.table {
    margin: 0 auto;
    border: 1px solid #969696;
    width: 90%;
}

.table-files th {
    color: #323232;
    border-bottom: 1px solid #969696;
    padding-bottom: 10px;
}

.table-columns td {

    border-bottom: 1px solid #969696;
    
}

.table tr {
    height: 55px;
}

.table-columns td p {

    display: flex;
    justify-content: center;
}

.table-columns3 td p {

    display: flex;
    justify-content: center;

}


.table-columns3 {
    border-bottom: 1px solid #969696;
}

.table-title {
    display: flex;
    background: linear-gradient(90deg, rgba(17,30,108,1) 17%, rgba(47,35,104,1) 39%, rgba(62,38,102,1) 51%, rgba(74,40,100,1) 69%, rgba(239,67,78,1) 100%);
    height: 60px;
    width: 90%;
    margin: 0 auto;
    margin-top: 30px;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.table-title h3 {
    margin-left: 10px;
    color: white;
}


.icon-cobro, .icono-metodo {
    width: 20px;
    height: 20px;
}

.table-transaccion, .table-metodo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.table-transaccion img {
    margin-right: 5px;
}

.table-metodo img {
    margin-right: 5px;
}
